import styled from "styled-components";
import {useEffect, useState} from "react";
import LogoImg from "../../assets/images/fir_crypto_logo@2x.png"

const aa = require("../../assets/voide/top_bg.mp4").default;


export const AutoWidget: React.FC = () => {


    const [height, setHeight] = useState(810);
    const [width, setwidth] = useState(0);
    const [postion, setPostion] = useState({left: 0, top: 0})

    let minHeight = 810;
    let minWidth = 1440;
    useEffect(() => {
        // eslint-disable-next-line no-restricted-globals
        let _height = screen.height
        let _width;
        let _postion = {
            left: 0,
            top: 0,
        };

        _width = _height * 1.7777;
        if (_height < 500) {
            setHeight(minHeight);
            _postion.top = (minHeight - _height) / 2;
            setwidth(minWidth);
        } else {
            setHeight(_height);
            setwidth(_width);
            _postion.left = (_width - document.body.clientWidth) / 2;
        }

        setPostion(_postion);
        // eslint-disable-next-line no-restricted-globals
    }, [screen.height])

    return <AutoStyle>

        <div className="inner">

            <video className={'video'} style={{
                height: "100%",
                width: "100%",
                // left: (0 - postion.left) + "px",
                // top: (0 - postion.top) + "px"
            }} loop autoPlay muted playsInline>
                <source src={aa}/>
            </video>
        </div>
        <div className="text">
            <img src={LogoImg} alt="" className="logo"/>
            <h4>Fir Capital is a private investment fund focusing on Blockchain Technology. We prefer to help innovators
                who can lead the change of Times.</h4>
        </div>
        {/*<h3>{msg}</h3>*/}
        {/*<ButtonWidget to={'/portfolio'}>Portfolio</ButtonWidget>*/}
    </AutoStyle>;
}


const AutoStyle = styled.div`
  //height: 100vh;
  position: relative;
  overflow: hidden;

  box-sizing: border-box;
  margin: 0 auto;
  width: 100vw;

  .inner {
    height: calc(100vw * 0.55);
  }

  .video {
    position: absolute;
    height: calc(100vw * 0.55);
  }

  .text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .logo {
      display: block;
      width: 230px;
      margin-bottom: 40px;
    }

    h4 {
      width: 60%;
      color: #F1F1F1;
      font-weight: bold;
      line-height: 1.6;
      font-size: 32px;
      letter-spacing: .02rem;
      text-align: center;


    }

  }


  @media (max-width: 768px) {
    min-width: 94%;
    padding: 20px 0;
    h3 {
      text-align: start;
    }
  }

`;
